// Utilities
import * as React from 'react';
// Components
import Target from '../../Components/Target/Target';

interface IState {
    currentScore: number,
    gameActive: boolean,
    topScore: number
    targets: JSX.Element[],
    spawnTimer: number
}

class GameControler extends React.Component<any, IState> {

    constructor(props: any) {
        super(props);
        this.state = {
            currentScore: 0,
            gameActive: false,
            topScore: 0,
            targets: [],
            spawnTimer: 350
        };
    };


    public componentDidMount = (): void => {
        const spawner = setInterval(() => {
            if(this.state.targets.length > 49) {
                this.setState(prevState => {
                    return Object.assign({}, prevState, {
                        currentScore: 0,
                        gameActive: false,
                        topScore: prevState.currentScore > prevState.topScore ? prevState.currentScore : prevState.topScore,
                        targets: [],
                        spawnTimer: 110
                    })
                })
        }
            if(this.state.gameActive) {
                setTimeout(() => {
                    this.spawnTarget();
                }, Math.round(Math.random() * Math.floor(this.state.spawnTimer)))
            } else {
                
            }
        }, this.state.spawnTimer);
    };
  


    public spawnTarget = (): void => {
        const left: number = Number((document.querySelector('.canvas') as HTMLElement).offsetWidth) - 50;
        const top: number = Number((document.querySelector('.canvas') as HTMLElement).offsetHeight) - 50;
        const positioning = {
            left: Math.floor(Math.random() * Math.floor(left)),
            top: Math.floor(Math.random() * Math.floor(top))
        }
        const key = Date.now();
        const newTarget: JSX.Element = <Target despawnTarget={this.despawnTarget} key={key} id={key} positioning={positioning} />
        this.setState(prevState => {
            return Object.assign({}, prevState, {
                ...prevState,
                targets: [...prevState.targets, newTarget]
            });
        });
    };

    public despawnTarget = (id: number): void => {
        const newTargetList = [...this.state.targets];
        newTargetList.splice(newTargetList.findIndex((target) => Number(target.key) === Number(id)), 1);
        this.setState(prevState => 
            Object.assign({}, prevState, {
                ...prevState,
                targets: newTargetList,
                currentScore: prevState.currentScore + 1
            })
        );
    };

    public startGame = (): void => {
        this.setState(prevState => {
            return Object.assign({}, prevState, {
                ...prevState,
                currentScore: 0,
                gameActive: true,
                targets: [],
                spawnTimer: 110
            })
        })
    }

    public render(): JSX.Element {
        const targets = [...this.state.targets];
        return (
            <div className="container">
                <div className="board">
                    <h3>Precision Tester</h3>
                    <p style={{color: 'red'}}>Game will end when 50 targets are on the board at once.</p>
                    <p>Current Score: {this.state.currentScore}</p>
                    <p>High Score: {this.state.topScore}</p>
                    <div className="canvas">
                        {targets}
                        {
                            !this.state.gameActive
                                ?   <button
                                        className="startBtn"
                                        onClick={this.startGame}
                                    >{
                                        this.state.topScore > 0
                                            ? 'Try Again'
                                            : 'Start Game'
                                    }</button>
                                : null
                        }
                        {
                            this.state.gameActive
                                ? <p style={{
                                    transform: `translate(-50%, -50%) scale(${targets.length/30})`
                                }}>{targets.length}</p>
                                : null
                        }
                    </div>
                </div>
            </div>
        );
    };
};

export default GameControler;