// Utilities
import * as React from 'react';

interface IProps {
    positioning: {
        top: number,
        left: number
    },
    id: number,
    despawnTarget: any
};

const Target = (props: IProps): JSX.Element => {
    const position: {[key: string]: number} = {
        ...props.positioning
    };
    return (
        <div onMouseDown={() => props.despawnTarget(props.id)} className="target" style={{left: position.left, top: position.top}} />
    );
};

export default Target;